.circle-go{
	opacity: 0;
}



@keyframes progress {
	from {
		stroke-dashoffset: 360;
	}

	to {
		stroke-dashoffset: 0;
	}
}

@keyframes zoomUp {
	0% {
	  transform: scale(1);
	}
	100% {
		transform: scale(1.4);
	}
}


.swiper-pagination-bullet {
	position: relative;
	@include flex(center, center);
	width: auto;
	height: auto;
	margin: 0 10px;
	background-color: transparent;
	border-radius: 50%;
	cursor: pointer;
	transition: 0.3s;
	opacity: 1;
	&:after{
		content: "";
		display: block;
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: #27364B;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
	&-active{
		&:after{
			background: #03A34D;
		}
		.circle-go {
			opacity: 1;
			fill: rgba(255, 255, 255, 0);
			stroke: #03A34D;
			stroke-width: 1;
			stroke-linecap: round;
			animation: progress 30s;
			stroke-dasharray: 360px;
			stroke-dashoffset: 360;
		}
	}
}


.form-control{
	display: block;
	width: 100%;
	height: 48px;
	background: #fff;
	border: none;
	font-weight: 500;
	appearance: none;
	padding: 0 15px;
	border-radius: 8px;
	outline: 0;
	border: 1px solid #E2E8F0;
	@include font-size(14);
	@include mq(){
		height: 40px;
	}
	&--area{
		width: 100%;
		height: 150px;
		resize: none;
	}
}



.slick-slider {
	touch-action: auto !important;
}